import { AnimationOption } from '@/data/CameraType';
import {
  EasingFunctionType,
  EasingModeType,
  getEasingFunctionTypeMap,
  getEasingModeMap,
} from '@/util/EasingFunctionType';

import ArgType from './ArgType';

const defaultSeconds = new AnimationOption().seconds; // default value

// EasingFunction
const functionsMap = getEasingFunctionTypeMap();
const functions = Object.keys(functionsMap);
const modesMap = getEasingModeMap();
const modes = Object.keys(modesMap);

export const animationArgs = {
  seconds: defaultSeconds,
  easingFunction: functions[0],
  easingMode: modes[0],
};

export const getAnimationArgs = (
  seconds: number,
  type: EasingFunctionType,
  mode: EasingModeType
) => {
  return {
    seconds: seconds,
    easingFunction: EasingFunctionType[type],
    easingMode: EasingModeType[mode],
  };
};

export const animationArgTypes = {
  seconds: ArgType.Range(0, 2, 0.05, 'Animation'),
  easingFunction: ArgType.Select(functions, 'Animation'),
  easingMode: ArgType.Select(modes, 'Animation'),
};
